// contexts/AuthContext.tsx
'use client';

import { createContext, useContext, useEffect, useState, ReactNode } from 'react';

type AuthContextType = {
  isAuthenticated: boolean | null; // null = loading, true/false = known
  user: any | null;
  checkAuth: () => Promise<void>;
  logout: () => void;
};

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: ReactNode }) {
  const [isAuthenticated, setIsAuthenticated] = useState<boolean | null>(null);
  const [user, setUser] = useState<any | null>(null);
  const [isChecking, setIsChecking] = useState(false);

  const checkAuth = async () => {
    // Prevent duplicate checks
    if (isChecking) {
      console.log('‚ö†Ô∏è Auth check already in progress, skipping');
      return;
    }

    setIsChecking(true);
    console.log('üîç Checking auth status...');

    try {
      const res = await fetch('/api/auth/me', {
        method: 'GET',
        credentials: 'include',
        cache: 'no-store',
      });

      if (res.ok) {
        const data = await res.json();
        setIsAuthenticated(true);
        setUser(data);
        console.log('‚úÖ User authenticated:', data.email);
      } else {
        setIsAuthenticated(false);
        setUser(null);
        console.log('‚ùå User not authenticated');
      }
    } catch (error) {
      console.error('‚ùå Auth check failed:', error);
      setIsAuthenticated(false);
      setUser(null);
    } finally {
      setIsChecking(false);
    }
  };

  const logout = () => {
    setIsAuthenticated(false);
    setUser(null);
  };

  // Check auth once on mount
  useEffect(() => {
    checkAuth();
  }, []);

  return (
    <AuthContext.Provider value={{ isAuthenticated, user, checkAuth, logout }}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within AuthProvider');
  }
  return context;
}